body{
	font-family: $fuente;
	font-weight: $regular;
	background: $primary;
	height: 100%;
}

@mixin contenedorDesktop{	
	padding-top: 60px;
	padding-bottom: 0px;
	max-width: 1280px;
	height: 100vh;	
	margin: auto;
	h2{
		font-size: 25px;
		text-align: center;
		font-weight: $bold;
		color: $secondary;
		background-color: $primary;
	}
	.productos{
		margin: auto;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		.boxproducto{
			float: left;
			padding: 20px;
			text-align: center;
			font-weight: $bold;
			margin: auto;
			h3{	
				text-align: center;
				font-size: 18px;
				font-weight: $light;
				margin-top: 30px;
				color: $tertiary;
			}
			h4{
				color: $accent;
				font-size: 20px;
			}
			p{
				color: $secondary;
			}
			.tachado{
				color: $accent;
				text-decoration: line-through;
			}
			.btn-primary{
				margin-left: 0px;
			}	
		}
	}	
}

@mixin contenedorMobile{
	.productos{
  		width: 70%;
		margin: auto;
		margin-top: 50px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		.boxproducto{
			width: 100%;
			padding: 20px;
			text-align: center;
			font-weight: bold;
			h3{	
				text-align: center;
				font-size: 18px;
				font-weight: $light;
				margin-top: 30px;
				color: $tertiary;
			}
			h4{
				color: $accent;
				font-size: 20px;
			}
			p{
				color: $secondary;
			}
			.btn-primary{
				margin-left: 0px;
			}	
		}
	}
}

@mixin boton{
	background-color: $accent;
	box-shadow: 0px 0px 15px -6px black;
	color: $primary;
	border-color: $no-color;
	transition: all 0.2s ease-in-out;
}

#header{
	.navbar{
		background-color: $secondary;
	}
}

#navbarNav{
	.navbar-nav{
		margin: auto;
		font-weight: $bold;
	}
	.nav-link{
		color: $primary;
	}
}

.btn-primary {
	background-color: $accent;
	color: $primary;
	border-color: $no-color;
	transition: all 0.2s ease-in-out;
	box-shadow: 0px 0px 15px -6px $tertiary;
	&:hover {	
		text-decoration: none;
		color: $primary;
		background-color: $secondary;
		box-shadow: 0px 0px 0px -6px $primary;
		border-color: $no-color;
	}
}

.btn-secondary {
  	background-color: $no-color;
  	color: $primary;
  	border-color: $no-color;
  	transition: all 0.2s ease-in-out;
  	font-weight: bold;
  	text-align: left;
	&:hover {
	  	box-shadow: 0px 0px 0px -6px black;
	  	text-decoration: none;
	  	color: $primary;
	  	background-color: $accent;
	  	border-color: $no-color;
	}
}

.dropdown:hover>.dropdown-menu {
	display: block;
}

.dropdown>.dropdown-toggle:active {
	pointer-events: none;
}

.navbar-nav li a:hover{
    cursor:pointer;
    transform: translateY(-3px);
    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
    color: $accent;
}

.carousel-item{
	h5{
		color: $primary;
		font-size: 30px;
		font-weight: $bold;
		text-shadow: $tertiary 0.1em 0.1em 0.2em;	
	}
	p{
		color: $primary;
		font-size: 25px;
		font-weight: $regular;
		text-shadow: $tertiary 0.1em 0.1em 0.2em;
	}
}

.videoplayer{
	height: 70vh;
	margin-top: 20px;
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.subtituloiphone{
	background-image: url("../media/iphonemorado1.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center top;
	height: 60vh;
	h2{
		margin-top: 30px;
		text-align: center;
		font-family: $fuenteN;
		font-size: 50px;
		font-weight: $bold;
		color: mediumpurple;
	}
}

.iphoneMoradoTexto{
	h3{
		margin-top: 30px;
		text-align: center;
		font-size: 30px;
		font-weight: $bold;
		color: mediumpurple;
		text-shadow: 5px 5px 10px mediumpurple;
	}
}

h1{
	padding: 10px;
	font-family: $fuenteN;
	font-size: 25px;
	font-weight: $bold;
	color: $primary;
	text-align: center;
	margin-bottom: 15px;
	background-color: $accent
}

h2{
	padding: 15px;
	color: $secondary;
	font-size: 30px;
}

h3{
	padding: 15px;
	color: $accent;
}

h4{
	color: #ff477e;
	font-weight: bold;
}

.parrafos{
	display: flex;
	flex-direction: row;
	text-align: center;
	font-size: 20px;
	padding: 25px;
	color: $secondary;
	font-family: $fuenteN;
	.textA{
		width: 50%;
		box-shadow: 5px 10px gray;
		padding: 10px;
	}
	.textB{
		width: 50%;
		box-shadow: 5px 10px gray;
		padding: 10px;
	}
}

#grilla{
	display: grid;
	grid-template-areas: 
	"iphone ipad macbook"
	"watch . airpods";
	grid-template-columns: 3fr 0fr 3fr;
	grid-template-rows: 2fr 2fr 2fr;
	grid-column-gap: 30px;
	grid-row-gap: 40px;
	height: 100vh;
	margin-top: 60px;
	padding: 20px;
	font-weight: $bold;
	font-size: 18px;
	color: $accent;
	.iphone{	
		grid-area: iphone;
		display: flex;
		flex-direction: column;
		align-items: center;
		&:hover{
		   	cursor:pointer;
		   	transform: translateY(-5px);
		    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
		}
	}	
	.ipad{
		grid-area: ipad;
		display: flex;
		flex-direction: column;
		align-items: center;
		&:hover{
			cursor:pointer;
			transform: translateY(-5px);
		    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
		}
	}
	.macbook{
		grid-area: macbook;
		justify-self: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		&:hover{
	    	cursor:pointer;
	    	transform: translateY(-5px);
	    	transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
		}
	}
	.watch{
		grid-area: watch;
		align-self: stretch;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-self: end;
		&:hover{
		    cursor:pointer;
		    transform: translateY(-5px);
		    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
		}
	}
	.airpods{
		grid-area: airpods;
		align-self: stretch;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-self: start;
		&:hover{
		    cursor:pointer;
		    transform: translateY(-5px);
		    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
		}
	}
}

.showroom{
	max-width: 100%;
	height: 40rem;
	margin: 30px 0px;
	display: flex;
	justify-content: center;
}

.subtitulo{
	background-color: red;
	color: $primary;
	text-align: center;
}

.logoPM{
	display: flex;
	justify-content: center;
}

#textFormat{
	padding: 50px;
	text-align: center;
	font-size: 20px;
	font-weight: $bold;
	font-family: $fuenteP;
	color: $tertiary;
	line-height: 1.5;
	p{
		box-shadow: 5px 10px;
		padding: 10px;
	}	
}
	
#info{
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
	height: 100vh;
	.datos{
		margin: auto;
		text-align: center;
		h2{
			font-size: 25px;
			font-weight: $bold;
		}
		h3{	
			font-size: 20px;
		}
		.redes{
			width: 80%;
			max-width: 1280px;
			margin: auto;
			display: block;
			width: 200px;
			float: center;
			h3{	
				font-size: 20px;
			}
			img{
				display: block;
				width: 60%;
				margin: auto;
			}
		}
	}	
	.mapa{
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin: auto;
	}
	
}


#contenedorOfertas{
	@include contenedorDesktop;
	.tachado{
		color: $accent;
		text-decoration: line-through;
	}
}

#contenedorIphone{
  	@include contenedorDesktop;
}
	
#contenedorIpad{
  	@include contenedorDesktop;
}

#contenedorMacbook{
  	@include contenedorDesktop;
}

#contenedorWatch{
  	@include contenedorDesktop;
}

#contenedorAirpods{
  	@include contenedorDesktop;
}



.contenedorForm{
    background-color: $secondary;
    width: 400px;
    padding: 30px;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 4px;
    font-family: $fuente;
    text-align: center;
    color: $primary;
    box-shadow: 7px 13px 37px $tertiary;
    .control{
		width: 100%;
	    background-color: $primary;
	    padding: 10px;
	    border-radius: 4px;
	    margin-bottom: 20px;
	    border: 1px solid $accent;
	    font-family: $fuente;
	    font-size: 18px;
	}
	select{
		text-align: center;
	}
	.boton{
	    width: 70%;
	    height: 45px;
	    margin-top: 15px;
	    border-radius: 4px;
	    border:none;
	    color: $primary;
	    text-transform: uppercase;
	    background: $accent;
	    font-size: 18px;
	}
	.boton:hover{
	    cursor:pointer;
	    transform: translateY(-3px);
	    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
	    box-shadow: 0px 0px 15px -6px $primary;
	}
	.boton:active{
	    transform:translateY(3px);
	}
	.control:focus{
	    height: 60px;
	}
}

.imgnos{
	display: flex;
	justify-content: center;
}

#footer{
	background: $secondary;
	height: 80px;
	width: 100%;
	font-weight: $bold;
	.botonera{
		width: 80%;
		max-width: 1280px;
		margin: auto;
		ul{
			list-style: none;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding-top: 20px;
			li a{
				text-decoration: none;
				color: $primary;
			}
			li a:hover{
			    cursor:pointer;
			    transform: translateY(-3px);
			    transition: all cubic-bezier(0.215, 0.610, 0.355, 1) .5s;
			    color: $accent;
			}
		}
	}
}


@media all and (max-width: 992px){
	.showroom{
		max-width: 100%;
		height: 20rem;
		margin: auto;
		display: flex;
		justify-content: center;
	}
}

@media all and (max-width: 768px){
