	.iphoneMoradoTexto{
		h3{
			margin-top: 30px;
			text-align: center;
			font-size: 18px;
			font-weight: $bold;
			color: mediumpurple;
		}
	}

	.showroom{
		max-width: 50%;
		height: 20rem;
		margin: auto;
		display: flex;
		justify-content: center;
	}
	#footer .botonera{
		width: 80%;
		max-width: 1280px;
		margin: 0;
